export function SingleProduct(props) {
    const convertPrice = (number) => {
        return number.toString()
            .split('')
            .reverse()
            .map((num, i, arr) => 
                (i % 3 === 0 && i !== 0 && i !== arr.length) ? num + ',' : num
            )
            .reverse()
            .join('');
    };

    const convertStars = (stars) => {
        let reducer = stars;
        let result = [];
        while (result.length !== 5) {
            (reducer >= 0.75) ?
                result.push(<img src="assets/icons/star-full.png" alt="full-star-icon"/>) :
                (reducer >= 0.25) ? result.push(<img src="assets/icons/star-half.png" alt="haft-star-icon"/>) :
                    result.push(<img src="assets/icons/star-empty.png" alt="empty-star-icon"/>);
            reducer--;
        }
        return result;
    }

    function deleteCartItem() {

    }

    function addToCart() {
        let cart = document.getElementById('cart-number');
        cart.innerText = Number(cart.innerText) + 1;
        const newNode = (
            <div className="product-thumbnail">
                <img className="thumbnail" src={props.image} />
                <div className="details">
                    <div class="header">
                        <span class="name">{props.name}</span>
                        <span className="price">
                            {convertPrice(props.salePrice) + 'đ'}
                            <span class="quantities"> x{1}</span>
                        </span>
                    </div>
                    <div class="footer">
                        <span class="categorize">{props.name}</span>
                        <button class="remove" onClick={deleteCartItem}>Xóa</button>
                    </div>
                </div>
            </div>
        );
        
        let children = Array.from(document.getElementById('yourcart').children).filter(item => item.className === 'product-thumbnail');

        ReactDOM.render(newNode, document.getElementById('yourcart'));
        
    }
    
    return(
        <div className="product-wrapper">
            <div className="product-image">
                <button><a href={props.href}>Mua ngay</a></button>
                <img role="button" src="assets/icons/cart.svg" alt="cart-icon" onClick={addToCart}/>
                <img src={props.image} alt="product"/>
                <div className="stars">
                    {convertStars(props.stars)}
                    <p>{'(' + props.stars.toFixed(1) + ' sao / ' + props.numberOfRatings.toFixed(0) + ' đánh giá)'}</p>
                </div>
            </div>
            <h2>{props.name}</h2>
            <p>{convertPrice(props.salePrice) + 'đ'}</p>
            <small>{convertPrice(props.originPrice) + 'đ'}</small>
        </div>
    );
}

class Category extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cart: []
        }
        this.updateCart = this.updateCart.bind(this);
    }

    updateCart(name, price, quantity, catigorize, imgsrc) {
        const item = <h1>Hello</h1>
        ReactDOM.render(item, document.getElementsByClassName('cart-tooltip')[0]);
    }

    render() {
        const settings = {
            dots: true,
            infinite: false,
            slidesToScroll: 2,
            slidesToShow: 4,
            childWidth: 300,
            outerDim: {
                width: 1200,
                height: 420
            },
            innerDim: {
                width: 1180,
                height: 422
            }
        }
        
        return (
            <div className="products-container">
                { <h2>{ this.props.category.name }</h2> }
                <Slider {...settings}>
                {
                    this.props.category.data.map(item =>
                        <SingleProduct updateCart={this.updateCart} {...item} />    
                    )
                }
                </Slider>
                <button>XEM TẤT CẢ</button>
            </div>
        );
    };
};

class Products extends Component {
    render() {
        return (
            <div className="all-products">
                <Category category={fashion} />
                <Category category={sports} />
                <Category category={versatile} />
            </div>
        );
    };
};

export default Products;