import React, { Component } from 'react';
import '../../styles/yourcart.css';

class ItemInCart extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
        this.handleDelete = this.handleDelete.bind(this);
    }

    handleDelete() {

    }

    render() {
        const {name, price, quantity, catigorize, imgsrc} = this.props;
        
        return (
            <div className="product-thumbnail">
                <img className="thumbnail" src={imgsrc} />
                <div className="details">
                    <div className="header">
                        <span className="name">{name}</span>
                        <span className="price">
                            {price}
                            <span className="quantities"> x{quantity}</span>
                        </span>
                    </div>
                    <div className="footer">
                        <span className="categorize">{catigorize}</span>
                        <button className="remove">Xóa</button>
                        
                    </div>
                </div>
            </div>
        );
    }
}

export default class YourCart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cart: []
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }

    handleMouseEnter() {
        let ct = document.getElementsByClassName('cart-tooltip-container')[0];
        ct.classList.add('show');
    }

    handleMouseLeave() {
        let ct = document.getElementsByClassName('cart-tooltip-container')[0];
        ct.classList.remove('show');
    }

    render() {
        return (
            <div className="your-cart" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
                <img src="assets/icons/cart.png" alt="cart-icon"/>
                <div>GIỎ HÀNG</div>
                <span id="cart-number">0</span>

                <div role="tooltip" className="cart-tooltip-container">
                    <div className="cart-tooltip">
                        <h2>SẢN PHẨM MỚI THÊM</h2>
        <div id="yourcart">BẠN CHƯA CÓ SẢN PHẨM NÀO!</div>
                    </div>                            
                </div>
            </div>
        );
    }
}
